/**************/
/*    # UX    */
/**************/

/* ## Header */
.mainHeader {
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
}
.sidebarHeader {
    padding: 0 2.25rem;
}
.sidebarHeader .logo {
    margin-right: 1rem;
}
.appType {
    padding: .175rem .375rem;
    font-size: .75rem;
}
.headerNav li a {
    height: 2.5rem;
    margin-right: 1rem;
    padding: .5rem .875rem;
    font-size: .875rem;
}
.searchBox {
    width: 100%;
    max-width: 400px;
    margin: 0 2.25rem 0 auto;
}
.searchBox form {
    position: relative;
}
.searchBox form input {
    width: 100%;
    height: 2.25rem;
    padding: 0 2.5rem 0 1rem;
    font-size: 1rem;
    border: none;
}
.searchBox form button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
}
.account {
    margin-right: 2.25rem;
    position: relative;
}
.accountIcon {
    width: 2.25rem;
    height: 2.25rem;
    justify-content: center;
}
.accountInfo {
    position: absolute;
    top: 2.5rem;
    right: 0;
}

/* ## Appmain */
.appMain {
    padding-top: 60px;
}
.appMain main > .row {
    margin: 0;
}
.homeContent {
    margin: 3rem auto;
}
.summaryDoc {
    margin-top: 3rem;
}
.summaryDoc h2 {
    margin-bottom: .5rem;
}
.summaryDoc ul.whiteBlock {
    padding: .75rem;
}
.summaryDoc ul .listPart a {
    padding: .75rem;
}
.summaryDoc ul .listPart a:after {
    content: '\e002';
    margin: 0 .5rem 0 auto;
    font-family: 'documentation-ico';
    font-size: 1rem;
    color: transparent;
}
.summaryDoc ul .listPart a:hover {
    background-color: var(--bg-main3-color);   
    border-radius: .5rem;
}
.summaryDoc ul .listPart a:hover:after {
    color: var(--text-main-color);
    transform: translateX(.5rem);
    transition: all ease .3s;
}
.logoThem {
    width: 1.5rem;
    margin-right: 1rem;
}

/* ### Sidebar */
.colSommaire {
    width: 28.125rem;
    height: calc(100vh - 60px - 4.5rem);
    padding: 0 2rem;
    position: fixed;
    top: calc(60px + 1.5rem);
    left: 0;
    overflow-y: scroll;
}
.colSommaire > div {
    position: relative;
}
.currentDoc.whiteBlock {
    display: flex;
    padding: 0;
    margin-bottom: 2rem;
    cursor: pointer;
}
.currentDocTitle {
    padding: 1rem;
    flex: 1 1 100%;
}
.colSommaire ul li {
    margin-top: 1rem;
}
.colSommaire ul.listDoc li {
    margin-top: 2rem;
}
.partItems .chapItems {
    display: none;
    margin: 1rem 0 2rem .75rem;
    padding: 1rem 0 1rem 1.75rem;
}
.partItems .active ~ .chapItems {
    display: block;
}

/* ### Page Partie*/
.documentation .colContent {
    min-height: calc(100vh - 60px);
    margin: 0 0 0 28.125rem;
    padding: 3rem;
}
.docTitle {
    margin-bottom: .5rem;
    font-weight: 700;
}
.blockChapter {
    margin-top: 5rem;
}
.nberChapter {
    margin-right: 1rem;
    font-size: 1rem;
    color: var(--main-doc-color);
}
.nberChapter:after {
    content: '';
    display: block;
    width: .25rem;
    height: .25rem;
    margin-left: .25rem;
    background-color: var(--main-doc-color);
    border-radius: 50%;
}
.blockChapter h2 {
    margin-bottom: 2rem;
    padding-bottom: 1rem;
}
.ucList .itemUC {
    margin-top: 1rem;
}
.titleUC {
    padding-bottom: .25rem;
}
.refInternet {
    font-size: .875rem;
}

/* ### Page UC */
.documentation.uc .colContent h1 {
    margin-bottom: 1.5rem;
}
.documentation.uc .colContent h2 {
    margin: 3rem 0 0 0;
}
.documentation.uc .colContent h3 {
    margin: 2rem 0 0 0;
}
.documentation.uc .colContent h4 {
    margin: 1rem 0 0 0;
}
.documentation.uc .content ul {
    margin: 1rem 0 0 1.75rem;
    list-style-type: disc;
}
.documentation.uc ul, .documentation.uc ul li {
    margin-top: 1rem;
}
.onThisPage {
    padding: 0 1.5rem;
    position: sticky;
    top: calc(60px + 4.5rem);
}
.onThisPage ul li ul {
    margin: 1.25rem 1.5rem;
}



/******************/
/*    # Design    */
/******************/

body {
    background-color: var(--bg-main3-color);
    color: var(--text-main-color);
}
.home .colContent, .documentation .colContent {
    background-color: var(--bg-main1-color);
    border-left: 1px solid var(--bg-main2-color);
}
.documentation.uc .colContent a {
    color: var(--main-doc-color);
}
.documentation.uc .onThisPage a {
    color: var(--text-third-color);
}
li a:hover {
    color: var(--main-doc-color);
    transition: all ease .3s;
}
li a.active {
    font-weight: 600;
    color: var(--main-doc-color);
}
.whiteBlock {
    padding: 1.5rem;
    border-radius: 1rem;
    background-color: var(--bg-main1-color);
    border: 1px solid var(--bg-main2-color);
}

/* ## Header */
.mainHeader {
    background-color: var(--bg-main1-color);
    border-bottom: 1px solid var(--bg-main2-color)
}
.appType {
    border-radius: .375rem;
    background-color: var(--bg-main2-color);
    color: var(--text-third-color);
}
.headerNav li a:before {
    margin-right: .875rem;
    font-family: "documentation-ico";
    font-size: 1rem;
    font-weight: 400;
}
.headerNav li.menuHome a:before {
    content: "\e009";
}
.headerNav li.menuModeles a:before {
    content: "\e008";
}
.searchBox form input {
    border-radius: 3rem;
    background-color: var(--bg-main3-color);
    border: 1px solid var(--bg-main2-color);
}
.searchBox form input:focus {
    outline: none;
}
.searchBox form button {
    border: none;
    background: none;
    color: var(--main-doc-color);
}
.searchBox form button:before {
    content: '\e00c';
    font-family: 'documentation-ico';
    font-size: 1rem;
}
.accountIcon {
    border-radius: 50%;
    font-weight: 600;
    background-color: var(--main-doc-color);
    color: var(--bg-main2-color);
}
.accountInfo {
    box-shadow: 0 10px 40px 20px rgba(26,32,44,0.08);
}
.backToSpace a {
    width: 60px;
    height: 60px;
    justify-content: center;
    border-left: 1px solid var(--bg-main2-color);
    border-bottom: 1px solid var(--bg-main2-color);
}
.backToSpace a:before {
    content: '\e003';
    width: 1rem;
    height: 1rem;
    font-family: 'documentation-ico';
    font-size: 1rem;
    transition: all ease .3s;
}
.backToSpace a:hover {
    background-color: var(--bg-main3-color);
}
.backToSpace a:hover:before {
    transform: rotate(90deg);
}

/* ## Appmain */
/* ### Page Partie */
.seeAllThem {
    flex: 1 0 3rem;
    justify-content: center;
    background-color: var(--bg-main3-color);
    color: var(--text-fourth-color);
    border-radius: 0 1rem 1rem 0;
    border-left: 1px solid var(--bg-main2-color);
}
.seeAllThem:after {
    content:'\e002';
    display: block;
    font-family: "documentation-ico";
    font-size: .875rem;
    transform: rotate(90deg);
    transition: transform ease .3s;
}
.active .seeAllThem:after {
    transform: rotate(270deg);
}
.active.currentDoc {
    border-color: var(--main-doc-color);
}
.navThem {
    position: absolute;
    top: calc(100% + 1rem);
    left: 0;
    bottom: 0;
}
.listDoc {
    box-shadow: 0 10px 40px 20px rgba(26,32,44,0.08);
}
.docTitle {
    color: var(--main-doc-color);
}
.blockChapter h2 {
    border-bottom: 1px solid var(--bg-main2-color);
}
.itemUCOuter:before {
    content: "\e008";
    font-family: "documentation-ico";
    font-size: 1.25rem;
    padding-right: 1rem;
}
.refInternet  {
    color: var(--text-fourth-color);
}
.partItems .chapItems {
    border-left: 1px solid var(--main-doc-color);    
}
.partItems .chapItems li a {
    color: var(--text-third-color);
}
.onThisPage {
    border-left: 1px solid var(--bg-main2-color);
}
.intro {
    font-weight: 600;
}
.notezle, .attention, .exemple {
    margin: 2.5rem 1.5rem;
    padding: 1.5rem;
    line-height: 1.4;
    font-weight: 400;
    border-radius: 1rem;
    background-color: var(--bg-main1-color);
    box-shadow: 0px 3px 6px rgba(26,32,44,0.08);
    position: relative;
}
.notezle:before, .attention:before, .exemple:before {
    display: inline-block;
    padding: 0 .5rem;
    font-weight: 600;
    background-color: var(--bg-main1-color);
    position: absolute;
    top: -.75rem;
}
.notezle {
    border: 1px solid var(--main-doc-color);
}
.attention {
    border: 1px solid #ff4f4f;
}
.exemple {
    border: 1px solid var(--text-third-color);
}
.notezle:before {
    content: "Notez-le";
    color: var(--main-doc-color);
}
.attention:before {
    content: "Attention";
    color: #ff4f4f;
}
.exemple:before {
    content: "Exemple";
    color: var(--text-third-color);
}

.colContent {
    transition: all ease .5s;
}
[class^="col-"] {
    transition: all ease .5s;
}

