/* # Global */
:root {
 --text-main-color: #1A202C; /* Gray 900 */
 --text-second-color: #fff; /* Blanc */
 --text-third-color: #4A5568; /* Gray 700 */
 --text-fourth-color: #a0aec0; /* Gray 500 */
 
 --bg-main1-color: #fff; /* Blanc */
 --bg-main2-color: #e2e8f0; /* Gray 300 */
 --bg-main3-color: #f7fafc; /* Gray 100 */

 --main-doc-color: #0096e6; /* Couleur de la doc */
}
*, *:before, *:after {
  box-sizing: border-box;
}
html {
  font-size: 16px;
}
body {
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, .heading-1 {
  margin: 0 0 1.5rem 0;
  font-size: 2rem;
  font-weight: 900;
}
h2, .heading-2 {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
  font-weight: 700;
}
h3, .heading-3 {
  margin: 0 0 1.25rem 0;
  font-size: 1.125rem;
  font-weight: 700;
}
h4, .heading-4 {
  margin: 0 0 1rem 0;
  font-size: 1rem;
  font-weight: 700;
}
p {
  margin: 1rem 0 0 0;
  line-height: 1.4;
}
a {
  text-decoration: none;
  color: #1A202C;
  color: var(--text-main-color);
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
table {
  margin: 1rem auto;
  border-radius: 1rem;
  border-collapse: collapse;
}
th, td {
  padding: .75rem;
  border: 1px solid #4A5568;
  border: 1px solid var(--text-third-color);
}
table tr td:first-child, table tr th:first-child {
 border-left: 0; 
}
table tr td:last-child, table tr th:last-child {
  border-right: 0; 
 }
ul > *:first-child, div > .row:first-child, * > p:first-child, * > h3:first-child {
  margin-top: 0 !important;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

/* ## Class */
.flex-items-center {
  display: flex;
  align-items: center;
}
.row {
  margin-bottom: 3rem;
}
.row:last-child {
  margin-bottom: 0;
}
button {
  padding: .25rem .75rem;
  font-weight: 400;
  font-size: .75rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
/**************/
/*    # UX    */
/**************/

/* ## Header */
.mainHeader {
    height: 60px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
}
.sidebarHeader {
    padding: 0 2.25rem;
}
.sidebarHeader .logo {
    margin-right: 1rem;
}
.appType {
    padding: .175rem .375rem;
    font-size: .75rem;
}
.headerNav li a {
    height: 2.5rem;
    margin-right: 1rem;
    padding: .5rem .875rem;
    font-size: .875rem;
}
.searchBox {
    width: 100%;
    max-width: 400px;
    margin: 0 2.25rem 0 auto;
}
.searchBox form {
    position: relative;
}
.searchBox form input {
    width: 100%;
    height: 2.25rem;
    padding: 0 2.5rem 0 1rem;
    font-size: 1rem;
    border: none;
}
.searchBox form button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
}
.account {
    margin-right: 2.25rem;
    position: relative;
}
.accountIcon {
    width: 2.25rem;
    height: 2.25rem;
    justify-content: center;
}
.accountInfo {
    position: absolute;
    top: 2.5rem;
    right: 0;
}

/* ## Appmain */
.appMain {
    padding-top: 60px;
}
.appMain main > .row {
    margin: 0;
}
.homeContent {
    margin: 3rem auto;
}
.summaryDoc {
    margin-top: 3rem;
}
.summaryDoc h2 {
    margin-bottom: .5rem;
}
.summaryDoc ul.whiteBlock {
    padding: .75rem;
}
.summaryDoc ul .listPart a {
    padding: .75rem;
}
.summaryDoc ul .listPart a:after {
    content: '\e002';
    margin: 0 .5rem 0 auto;
    font-family: 'documentation-ico';
    font-size: 1rem;
    color: transparent;
}
.summaryDoc ul .listPart a:hover {
    background-color: var(--bg-main3-color);   
    border-radius: .5rem;
}
.summaryDoc ul .listPart a:hover:after {
    color: var(--text-main-color);
    transform: translateX(.5rem);
    transition: all ease .3s;
}
.logoThem {
    width: 1.5rem;
    margin-right: 1rem;
}

/* ### Sidebar */
.colSommaire {
    width: 28.125rem;
    height: calc(100vh - 60px - 4.5rem);
    padding: 0 2rem;
    position: fixed;
    top: calc(60px + 1.5rem);
    left: 0;
    overflow-y: scroll;
}
.colSommaire > div {
    position: relative;
}
.currentDoc.whiteBlock {
    display: flex;
    padding: 0;
    margin-bottom: 2rem;
    cursor: pointer;
}
.currentDocTitle {
    padding: 1rem;
    flex: 1 1 100%;
}
.colSommaire ul li {
    margin-top: 1rem;
}
.colSommaire ul.listDoc li {
    margin-top: 2rem;
}
.partItems .chapItems {
    display: none;
    margin: 1rem 0 2rem .75rem;
    padding: 1rem 0 1rem 1.75rem;
}
.partItems .active ~ .chapItems {
    display: block;
}

/* ### Page Partie*/
.documentation .colContent {
    min-height: calc(100vh - 60px);
    margin: 0 0 0 28.125rem;
    padding: 3rem;
}
.docTitle {
    margin-bottom: .5rem;
    font-weight: 700;
}
.blockChapter {
    margin-top: 5rem;
}
.nberChapter {
    margin-right: 1rem;
    font-size: 1rem;
    color: var(--main-doc-color);
}
.nberChapter:after {
    content: '';
    display: block;
    width: .25rem;
    height: .25rem;
    margin-left: .25rem;
    background-color: var(--main-doc-color);
    border-radius: 50%;
}
.blockChapter h2 {
    margin-bottom: 2rem;
    padding-bottom: 1rem;
}
.ucList .itemUC {
    margin-top: 1rem;
}
.titleUC {
    padding-bottom: .25rem;
}
.refInternet {
    font-size: .875rem;
}

/* ### Page UC */
.documentation.uc .colContent h1 {
    margin-bottom: 1.5rem;
}
.documentation.uc .colContent h2 {
    margin: 3rem 0 0 0;
}
.documentation.uc .colContent h3 {
    margin: 2rem 0 0 0;
}
.documentation.uc .colContent h4 {
    margin: 1rem 0 0 0;
}
.documentation.uc .content ul {
    margin: 1rem 0 0 1.75rem;
    list-style-type: disc;
}
.documentation.uc ul, .documentation.uc ul li {
    margin-top: 1rem;
}
.onThisPage {
    padding: 0 1.5rem;
    position: -webkit-sticky;
    position: sticky;
    top: calc(60px + 4.5rem);
}
.onThisPage ul li ul {
    margin: 1.25rem 1.5rem;
}



/******************/
/*    # Design    */
/******************/

body {
    background-color: var(--bg-main3-color);
    color: var(--text-main-color);
}
.home .colContent, .documentation .colContent {
    background-color: var(--bg-main1-color);
    border-left: 1px solid var(--bg-main2-color);
}
.documentation.uc .colContent a {
    color: var(--main-doc-color);
}
.documentation.uc .onThisPage a {
    color: var(--text-third-color);
}
li a:hover {
    color: var(--main-doc-color);
    transition: all ease .3s;
}
li a.active {
    font-weight: 600;
    color: var(--main-doc-color);
}
.whiteBlock {
    padding: 1.5rem;
    border-radius: 1rem;
    background-color: var(--bg-main1-color);
    border: 1px solid var(--bg-main2-color);
}

