}
.backToSpace a {
    width: 60px;
    height: 60px;
    justify-content: center;
    border-left: 1px solid var(--bg-main2-color);
    border-bottom: 1px solid var(--bg-main2-color);
}
.backToSpace a:before {
    content: '\e003';
    width: 1rem;
    height: 1rem;
    font-family: 'documentation-ico';
    font-size: 1rem;
    transition: all ease .3s;
}
.backToSpace a:hover {
    background-color: var(--bg-main3-color);
}
.backToSpace a:hover:before {
    transform: rotate(90deg);
}

/* ## Appmain */
/* ### Page Partie */
.seeAllThem {
    flex: 1 0 3rem;
    justify-content: center;
    background-color: var(--bg-main3-color);
    color: var(--text-fourth-color);
    border-radius: 0 1rem 1rem 0;
    border-left: 1px solid var(--bg-main2-color);
}
.seeAllThem:after {
    content:'\e002';
    display: block;
    font-family: "documentation-ico";
    font-size: .875rem;
    transform: rotate(90deg);
    transition: transform ease .3s;
}
