    transform: rotate(270deg);
}
.active.currentDoc {
    border-color: var(--main-doc-color);
}
.navThem {
    position: absolute;
    top: calc(100% + 1rem);
    left: 0;
    bottom: 0;
}
.listDoc {
    box-shadow: 0 10px 40px 20px rgba(26,32,44,0.08);
}
.docTitle {
